import doc from '@/assets/doc.json'
import FunctionDescription from '@/types/FunctionDescription'

export default class DocParser {
  public static getFunctions(): FunctionDescription[] {
    const boussoleFuncsAndAttributes = doc.children[0].children
    const publicFunctions: FunctionDescription[] = []
    if (boussoleFuncsAndAttributes) {
      for (const element of boussoleFuncsAndAttributes) {
        const publicFunc = element as any
        if (publicFunc.flags.isPublic) {
          const staticPrefix = publicFunc.flags.isStatic ? 'static ': '';
          const funcName = publicFunc.signatures[0].name;
          const params = [];
          const returnType = publicFunc.signatures[0].type.name
          if (publicFunc.signatures[0].parameters) {
            for (const param of publicFunc.signatures[0].parameters) {
              params.push(
                {
                  name: param.name,
                  type: param.type.name,
                  description: param.comment? param.comment.summary[0].text : ''
                }
                )
              }
            }
          let signatureParams = ''
          params.forEach((param, index) => signatureParams = signatureParams.concat((index !== 0 ?', ':''), `${param.name}: ${param.type}`))
          publicFunctions.push({
            name: publicFunc.signatures[0].name,
            description: publicFunc.signatures[0].comment? publicFunc.signatures[0].comment.summary[0].text: '',
            signature: `${staticPrefix}${funcName}(${signatureParams}): ${returnType}`,
            params: params
          })
        }
      }
    }
    return publicFunctions;
  }
}